/* ======= Doc Styling ======= */
.doc-wrapper {
    padding: 45px 0;
    background: $smoky-grey;
}

.doc-body {
    position: relative;
}



.doc-content {
    //margin-left: 230px;
}


.doc-section {
    padding-top: 15px;
    padding-bottom: 15px;
    .section-title {
        font-size: 26px;
        margin-top: 0;
        margin-bottom: 0;
        font-weight: bold;
        padding-bottom: 10px;
        border-bottom: 1px solid darken($divider, 10%);
    }

    h1 {
        font-size: 24px;
        font-weight: bold;
    }
    h2 {
        font-size: 22px;
        font-weight: bold;
    }
    h3 {
        font-size: 20px;
        font-weight: bold;
    }
    h4 {
        font-size: 18px;
        font-weight: bold;
    }
    h5 {
        font-size: 16px;
        font-weight: bold;
    }
    h6 {
        font-size: 14px;
        font-weight: bold;
    }
}

.section-block {
    padding-top: 15px;
    padding-bottom: 15px;
    .block-title {
        margin-top: 0;
    }
    .list {
        &>li {
            margin-bottom: 10px;
        }
        ul {
            &> li {
                margin-top:5px;
            }
        }
    }

}

.question {

    font-weight: 400 !important;
    color: darken($color-primary, 5%);
    .body-green {
        color: darken($color-green, 5%);
    }
    .body-blue & {
        color: darken($color-blue, 5%);
    }
    .body-orange &{
        color: darken($color-orange, 5%);
    }
    .body-pink &{
        color: darken($color-pink, 5%);
    }
    .body-purple &{
        color: darken($color-purple, 5%);
    }
    .svg-inline--fa {
        @include opacity(0.6);
        position: relative;
        top: -2px;
    }
    .badge {
        font-size: 11px;
        vertical-align: middle;
    }
}

.answer {
    color: $text-color-secondary;
}

.code-block {
    margin-top: 30px;
    margin-bottom: 30px;
}

.callout-block {
    padding: 30px;
    @include border-radius(4px);
    position: relative;
    margin-bottom: 30px;
    a {
        color: rgba(0, 0, 0, 0.55) !important;
        &:hover {
            color: rgba(0, 0, 0, 0.65) !important;
        }
    }
    .icon-holder {
        font-size: 30px;
        position: absolute;
        left: 30px;
        top: 30px;
        color: rgba(0, 0, 0, 0.25);
    }
    .content {
        margin-left: 60px;
        p {
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
    .callout-title {
        margin-top: 0;
        margin-bottom: 5px;
        color: rgba(0, 0, 0, 0.65);
    }
}

.callout-info {
    background: $color-blue;
    color: #fff;
}

.callout-success {
    background: $color-green;
    color: #fff;
}

.callout-warning {
    background: $color-orange;
    color: #fff;
}

.callout-danger {
    background: $color-red;
    color: #fff;
}

.table>thead>tr>th {
    border-bottom-color: lighten($color-primary, 20%);
    .body-green & {
        border-bottom-color: lighten($color-green, 20%);
    }
    .body-blue & {
        border-bottom-color: lighten($color-blue, 20%);
    }
    .body-orange & {
        border-bottom-color: lighten($color-orange, 20%);
    }
    .body-pink & {
        border-bottom-color: lighten($color-pink, 20%);
    }
    .body-purple & {
        border-bottom-color: lighten($color-purple, 20%);
    }
}
.table-bordered>thead>tr>th {
    border-bottom-color: inherit;
}

.table-striped>tbody>tr:nth-of-type(odd) {
    background-color: $smoky-white;
}


.screenshot-holder {
    margin-top: 15px;
    margin-bottom: 15px;
    position: relative;
    text-align: center;
    img {
        border: 1px solid $divider;
    }
    .mask {
        display: block;
        visibility: hidden;
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        background: rgba(0, 0, 0, 0.25);
        cursor: pointer;
        text-decoration: none;
        .svg-inline--fa {
            color: #fff;
            font-size: 42px;
            display: block;
            position: absolute;
            left: 50%;
            top: 50%;
            margin-left: -21px;
            margin-top: -21px;

        }
    }
    &:hover {
        .mask {
            visibility: visible;
        }
    }
}

.jumbotron {
    h1 {
        font-size: 28px;
        margin-top: 0;
        margin-bottom: 30px;
    }
}




.theme-card {
    text-align: center;
    border: 1px solid darken($divider, 5%);
    @include border-radius(4px);
    position: relative;
    height: 100%;

    .card-block {
        padding: 15px;
    }
    .mask {
        display: block;
        visibility: hidden;
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        background: rgba(0, 0, 0, 0.25);
        cursor: pointer;
        text-decoration: none;
        .icon {

            color: #fff;
            font-size: 42px;
            margin-top: 25%;
        }
    }
    &:hover {
        .mask {
            visibility: visible;
        }
    }
}


/* Color Schemes */
