/* Sidebar */

.doc-sidebar {


}

.doc-nav {
	position:absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.sticky {
	position: -webkit-sticky;
    position: -moz-sticky;
    position: -ms-sticky;
    position: -o-sticky;
    position: sticky;
    top: 0;
}

.doc-menu {
    list-style: none;
    margin-right: 10px;

   .nav-link {
        margin-bottom: 5px;
        display: block;
        color: $text-color-secondary;
        &:hover, &:focus {
            color: $text-color;
            text-decoration: none;
            background: none;

        }
        .nav {

        }
        &.active {

            background: none;
            color: $color-primary;
            font-weight: 600;
            .body-green & {
                color: #fff;
                border-color: $color-green;
                background-color:$color-green;
            }
            .body-blue & {
                color: #fff;
                border-color: $color-blue;
                background-color:$color-blue;

            }
            .body-orange & {
                color: #fff;
                border-color: $color-orange;
                background-color:$color-orange;

            }
            .body-red & {
                color: #fff;
                border-color: $color-red;
                background-color:$color-red;

            }
            .body-pink & {
                color: #fff;
                border-color: $color-pink;
                background-color:$color-pink;

            }
            .body-purple & {
                color: #fff;
                border-color: $color-purple;
                background-color:$color-purple;

            }



        }
    }
}
.doc-sub-menu {
    list-style: none;
    padding-left: 0;
    .nav-link {
        margin-bottom: 5px;
        font-size: 12px;
            display: block;
            color: $text-color-secondary;
            padding: 5px;
            padding-left: 14px;
            margin-left: 20px;
            background: none;
            &:hover{
                color: $text-color;
                text-decoration: none;
                background: none;
            }
            &:focus {
                background: none;
            }

            &.active {

                background: none;
                color: $color-primary;
                .body-green & {
                    color: #fff;
                    background-color:$color-green;
                }
                .body-blue & {
                    color: #fff;
                    background-color:$color-blue;

                }
                .body-orange & {
                    color: #fff;
                    background-color:$color-orange;

                }
                .body-red & {
                    color: #fff;
                    background-color:$color-red;

                }
                .body-pink & {
                    color: #fff;
                    background-color:$color-pink;

                }
                .body-purple & {
                    color: #fff;
                    background-color:$color-purple;

                }

            }
    }
}

